.content > * {
  color: #fff;
  margin-bottom: 1rem;
}

.current .content {
  opacity: 1;
  transform: translateX(0);
  transition: all 0.5s ease;
}

.arrow {
  background-color: transparent;
  color: rgb(255, 255, 255);
  cursor: pointer;
  height: 2rem;
  width: 2rem;
  border-radius: 50%;
  position: absolute;
  z-index: 999;
}
.arrow:hover {
  color: #777;
}

.next {
  top: 35%;
  right: 1.5rem;
}
.prev {
  top: 35%;
  left: 1.5rem;
}

hr {
  height: 2px;
  background: white;
  width: 50%;
}
.navbar {
  position: fixed;
  top: 0;
  padding-top: 10px;
  width: 100%;
  /* background-color: rgba(119, 119, 119, 0.313); */
}

nav {
  display: flex;
  align-items: center;
  justify-content: space-around;
}
