@import url("https://fonts.googleapis.com/css2?family=Montagu+Slab:opsz,wght@16..144,300;16..144,400;16..144,600;16..144,700&family=Open-Sans:wght@300&display=swap");

.facts {
  height: 100%;
  overflow: hidden;
  position: relative;
  background-image: url("https://firebasestorage.googleapis.com/v0/b/divine-public-school-e196f.appspot.com/o/websiteImages%2Fabout%2FDSCN0174%20(1).jpg?alt=media&token=90948ec4-e7d5-4bd7-8fc9-86dab40cee9b");
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  object-fit: fill;
  width: 100%;
  backdrop-filter: blur(5px);
}

.facts:before {
  content: "";
  position: absolute;
  z-index: -1;
  background: rgba(246, 196, 16, 0.063);
  width: 100%;
  height: 1000px;
  backdrop-filter: blur(5px);
}
.factOverlay {
  height: 800px;
  margin: 10em auto;
  width: 50%;
}

.factData {
  padding: 3rem 5rem;
  /* height: 50%; */
  background-color: rgb(208, 171, 37);
}
.factData h2 {
  font-size: 2rem;
  letter-spacing: 5px;
}
.factData h1 {
  font-size: 3rem;
  letter-spacing: 10px;
  color: rgb(29, 22, 101);
}
.factData p {
  font-size: 1.5rem;
  line-height: 2;
}

@media (max-width: 1440px) {
  .factOverlay {
    width: 80%;
    margin: 8em auto;
  }
}
@media (max-width: 768px) {
  .factData p {
    font-size: 1rem;
    line-height: 2;
  }
  .factData h1 {
    font-size: 2rem;
    line-height: 2;
  }
  .factData h2 {
    font-size: 1.5rem;
    line-height: 2;
  }
  .factData {
    padding: 1rem 2rem;
  }
  .factOverlay {
    width: 90%;
    height: 500px;
  }
}
